.wp-block-create-block-hero-block {
	background-color: #21759b;
	color: #fff;
	padding: 2px;
}

.card {
	background-color: #ffebfb;
	display: flex;
	align-items: center;
	justify-content: center;
	.card__content {
		display: flex;
		align-items: center;
		justify-content: center;
		flex-direction: column;
		font-family: "Nunito", sans-serif;
		color: #4a4d5e;
		margin: 15px;
		width: 50%;
		.card__title {
			margin: 15px;
			max-width: 800px;
			@media (max-width: 800px) {
				font-size: 36px;
			}
		}

		.card__body {
			margin: 15px;
			max-width: 800px;
		}
	}
	.card__image__wrapper {
		display: flex;
		width: 50%;
		align-self: stretch;
		.card__image {
			width: 100%;
			object-fit: cover;
		}
	}
}

/* Mobile */

@media screen and (max-width: 480px) {
	.card {
		flex-direction: column;
		.card__image__wrapper {
			width: auto;
			height: 50%;
		}
	}
}
